body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Helvetica', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.aspect-ratio {
  position: relative;
  height: 0;
}

.aspect-ratio > div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.aspect-ratio > div > * {
  width: 100%;
  height: 100%;
}
